    font-size: 1rem;
  }

  .restaurant-categories h4,
  .restaurant-menus h4,
  .restaurant-reviews h4 {
    font-size: 1.6rem;
  }

  .restaurant-menus li {
    width: 100%;
  }
}

@media (max-width: 1200px) {
  .hero-element img {
    width: 100%; /* Membuat gambar full width */
    height: auto; /* Menyesuaikan tinggi gambar agar proporsional */
  }
}

